export default {
	data() {
		return {
			topList: [],
			isLoading: true
		}
	},
	components: {
		musichead,
		mForSkeleton
	},
	onLoad() {
		topList().then((res)=>{
			if(res.length){
				setTimeout(()=>{
					console.log(res)
					this.topList = res;
					this.isLoading = false;
				},500)
				
			}
		});
	},
	methods: {
		hide(){
			this.isLoading = false;
		},
		handleToList(listId){
			uni.navigateTo({
				url: '/pages/list/list?listId='+listId,
				success: res => {
					
				},
				
			})
		},
		handleToSearch(){
			uni.navigateTo({
				url: '/pages/search/search'
			})
		}
	
	}
}
